import React from 'react'
import loadable from '@loadable/component'
import { Switch, Route, Redirect } from 'react-router'
import { createBrowserHistory } from 'history'
import config from '@/config/config'

import home from '@/views/home/home.js'
import Header from '@/components/header/Header.js'
import Footer from '@/components/footer/Footer'
import PageContainer from '@/components/page_container/PageContainer.js'

const basename = process.env.NODE_ENV !== 'development' ? config.outputDir : ''
const Error404 = loadable(() => import('@/views/common/Error404.js'))
const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/home',
    Header,
    Main: home,
    Footer
  },
  {
    path: '/games',
    meta: { name: 2 },
    Header,
    Main: loadable(() => import('@/views/games/games.js'))
  },
  {
    path: '/promotions',
    meta: { name: 3 },
    isPromoHeader: true,
    Main: loadable(() => import('@/views/promotions/promotions.js'))
  }
]
export const history = createBrowserHistory({
  basename
})

export default function Routes() {
  return (
    <Switch>
      {routes.map((route, index) => {
        return (
          <Route
            key={index}
            path={route.path}
            exact={!route.exact}
            strict={route.strict}
            render={props => {
              return route.redirect ? <Redirect to={route.redirect}/> : <PageContainer route={route} routeProps={props}/>
            }}/>
        )
      })}
      <Route path='*' component={Error404}/>
    </Switch>
  )
}

history.listen((location, action) => {
  // console.log(action, location)
})
